.blockName {
	position: relative;
	padding: 120px 0;

	&:nth-child(odd) {
		background-color: #f0f2f7; }

	@media screen and (max-width: 700px) {
		padding: 100px 0; }
	@media screen and (max-width: 500px) {
		padding: 70px 0; }
	@media screen and (max-width: 420px) {
		padding: 60px 0; } }

.container {
	width: 80%;
	margin: 0 auto;
	@media screen and (max-width: 1160px) {
		width: 90%; }
	@media screen and (max-width: 900px) {
		width: 100%; } }

.content {
	text-align: center;
	border: 1px solid gray; }

.blockTitle {
	width: 100%;
	display: flex;
	align-items: center;
	flex-direction: column;
	text-align: justify;

	& h2 {
		font-size: 60px;
		font-weight: 800;
		line-height: 60px;
		letter-spacing: 5px;
		text-transform: uppercase;
		color: #2c3d4f;
		@media screen and (max-width: 700px) {
			font-size: 45px; }
		@media screen and (max-width: 500px) {
			font-size: 35px; }
		@media screen and (max-width: 400px) {
			font-size: 30px; } }

	& p {
		color: #7e868e;
		font-size: 16px;
		line-height: 28px;
		text-align: left;
		padding-bottom: 10px; }

	& p span {
		font-weight: bold;
		color: #7e878f; } }

.border {
	display: block;
	width: 60px;
	height: 3px;
	margin: 40px auto;
	background-color: #f54f36;
	@media screen and (max-width: 700px) {
		margin: 30px auto; } }

.button {
	background: #f54f36;
	border-radius: 5px;
	padding: 15px 25px;
	color: #ffffff;
	display: inline-block;
	text-align: center;
	text-transform: uppercase;
	width: 80%;
	border: none;
	cursor: pointer;
	font-size: 16px;
	font-weight: bold;

	&:active, &:focus {
		outline: none; }

	&:hover {
		background: #d14734 linear-gradient(to bottom, #dd4c38 5%, #f54f36 100%); }

	&:active {
		position: relative;
		top: 1px; } }
