.insertion {
	padding: 145px 0;
	display: flex;
	background-image: url(../../assets/img/workplaceBgr.jpg);
	background-attachment: fixed !important;
	background-position: center top;
	background-size: cover;
	position: relative;

	&:before {
		position: absolute;
		content: '';
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: rgba(0,0,0,0.8);
		z-index: 0; }
	.content {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 0 15px;
		& h1 {
			color: white;
			text-align: center;
			line-height: 1em;
			font-weight: bold;
			font-size: 36px; }
		& button {
			padding: 17px 52px;
			margin-top: 80px;
			width: 200px;
			font-size: 14px;
			font-weight: bold; } } }
